#team .member {
  text-align: center;
  margin-bottom: 20px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
}

#team .member .member-info {
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  transition: 0.2s;
}

#team .member .member-info-content {
  margin-top: 50px;
  transition: margin 0.2s;
}

#team .member:hover .member-info {
  background: rgba(0, 62, 128, 0.7);
  opacity: 1;
  transition: 0.4s;
}

#team .member h4 {
  font-weight: 700;
  margin-bottom: 2px;
  font-size: 18px;
  color: #fff;
}

#team .member span {
  font-style: italic;
  display: block;
  font-size: 13px;
  color: #fff;
}

#team .member .social a {
  transition: none;
  color: #fff;
  padding: 5px;
  border: 1px solid #fff;
  border-radius: 5px;
}

.social {
  margin-top: 5px;
}

@media only screen and (max-width: 600px) {
  #team .member .member-info-content {
    margin-bottom: 30px;
  }

  #team .member .member-info-content h4 {
    font-size: 10px;
  }

  #team .member .member-info-content span {
    font-size: 10px;
  }
}

.our-team {
  border-radius: 10px;
  text-align: center;
  padding: 20px 10px 30px;
}

.our-team .pic {
  text-align: center;
  margin-bottom: 20px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
}

.our-team:hover {
  background: #f4f4f4;
  border-radius: 10px;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
}

.pic img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.our-team .title {
  font-weight: 600;
  color: #010101;
  text-transform: uppercase;
  display: block;
  font-size: 20px;
}

.our-team .post {
  color: #000000;
  text-transform: capitalize;
  display: block;
  font-weight: 200;
  font-size: 16px;
}

.title-wrapper, .post-wrapper {
  display: flex;
  justify-content: center;
  min-height: 50px;
}

.title-wrapper {
  align-items: flex-end;
}

.post-wrapper {
  align-items: flex-start;
}

.our-team .social {
  padding: 0;
  margin: 0;
  list-style: none;
}

.our-team .social li {
  display: inline-block;

  margin-right: 5px;
}

.our-team .social li a {
  border-radius: 50%;
  font-size: 15px;
  color: #101010;
  display: block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  transition: all 0.5s ease 0s;
}

.our-team .social li a:hover {
  background: #00629b;
  color: #fff;
}

@media only screen and (max-width: 479px) {
  .our-team .title {
    font-size: 0.98em;
    overflow-wrap: anywhere;
  }
}

