.stat {
  display: flex;
  align-items: stretch;

}
.stat .stat-item {
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  align-items: stretch;
  padding: 0 1rem;
  height:200px;
}
.stat .stat-item .stat-card {
  border: 1px solid #edeff1;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 1.5rem 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}
.stat .stat-item .stat-card .stat-title {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}
.stat .stat-item .stat-card .stat-title .prefix {
  margin-bottom: 0.125rem;
  font-size: 0.75rem;
  color: #b5bbc3;
}
.stat .stat-item .stat-card .stat-title .value {
  font-size: 2rem;
  color: #45505a;
}
.stat .stat-item .stat-card .stat-title .stat-change {
  margin-top: 0.125rem;
  font-size: 0.75rem;
  align-self: flex-start;
  display: inline-block;
  margin-top: 0.25rem;
  padding: 0.25rem;
  border-radius: 10px;
}
.stat .stat-item .stat-card .stat-title .stat-change.up {
  background-color: #cceeff;
  color: #006699;
}
.stat .stat-item .stat-card .stat-title .stat-change.up i {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.stat .stat-item .stat-card .stat-title .stat-change.down {
  background-color: #f2dddd;
  color: #bc4749;
}
.stat .stat-item .stat-card .stat-title .stat-change.down i {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.stat .stat-item .stat-card .stat-icon {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 5px;
  width: 150px;
  height: 150px;
}
.stat .stat-item .stat-card .stat-icon svg {
  width: 100%;
  height: 100%;
}
.stat.stat-bordered {
  margin: 0;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}
.stat.stat-bordered .stat-item {
  padding: 0;
}
.stat.stat-bordered .stat-item:first-of-type .stat-card {
  border-radius: 0.5rem 0 0 0.5rem;
}
.stat.stat-bordered .stat-item:not(:first-of-type) .stat-card {
  border-left: 0;
}
.stat.stat-bordered .stat-item:last-of-type .stat-card {
  border-radius: 0 0.5rem 0.5rem 0;
}
.stat.stat-bordered .stat-item .stat-card {
  box-shadow: none;
  border-radius: 0;
}
.stat.stat-bordered .stat-item .stat-card .stat-title .prefix {
  color: #2f363d;
}
.stat.stat-bordered .stat-item .stat-card .stat-title .value {
  color: #005580;
}
.stat.stat-bordered .stat-item .stat-card .stat-icon {
  right: 1rem;
}

