}

@-webkit-keyframes blinkColor {
  78% {
    fill: #006699;
  }
  79% {
    fill: #d1d5da;
  }
  81% {
    fill: #006699;
  }
  82% {
    fill: #d1d5da;
  }
  83% {
    fill: #006699;
  }
  92% {
    fill: #d1d5da;
  }
  92.5% {
    fill: #006699;
  }
}

@keyframes blinkColor {
  78% {
    fill: #006699;
  }
