.shownews {
  font-weight: 700;
  color: #fff;
  width: 600px;
  height: 100px;
  margin: 50px auto;
  text-transform: capitalize;
  font-family: "Open", sans-serif;
  display: grid;
  grid-template-columns: -webkit-max-content auto;
  grid-template-columns: max-content auto;
  grid-column-gap: 10px;

}

.shownews .title {
  background: rgb(7, 43, 82);
  display: inline-block;
  padding: 10px;
  font-family: 'Ubuntu', sans-serif !important;
  -webkit-clip-path: polygon(0px 0px, 100% 0, 100% 100%, 0px 100%);
          clip-path: polygon(0px 0px, 100% 0, 100% 100%, 0px 100%);
  -webkit-animation: mask 0.5s;
          animation: mask 0.5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  border-radius: 0 0 0 0;
  width: 100px;
  text-align: center;


}

.shownews .title span {
  display: block;
  font-size: 50px;
}

.shownews #news span#text {
  display: block;
  font-size: 20px;
}

.shownews #news span#para {
  display: block;
  font-size: 13px;
}

.shownews #news {
  background: #00629b;
  display: inline-block;
  padding: 16px 16px;
  border-radius: 0 15px 15px 0;
  font-family: 'Ubuntu', sans-serif;
  -webkit-clip-path: polygon(100% 100%, 100% 100%, 100% 100%, 100% 100%);
          clip-path: polygon(100% 100%, 100% 100%, 100% 100%, 100% 100%);
  -webkit-animation: mask 2s;
          animation: mask 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.shownews #news a {
  color: #FFF;
  text-decoration: none;
}

@-webkit-keyframes mask {
  0% {
    -webkit-clip-path: polygon(0px 0px, 0% 0, 0% 100%, 0px 100%);
            clip-path: polygon(0px 0px, 0% 0, 0% 100%, 0px 100%);
  }
  100% {
    -webkit-clip-path: polygon(0px 0px, 100% 0, 100% 100%, 0px 100%);
            clip-path: polygon(0px 0px, 100% 0, 100% 100%, 0px 100%);
  }
}

@keyframes mask {
  0% {
    -webkit-clip-path: polygon(0px 0px, 0% 0, 0% 100%, 0px 100%);
            clip-path: polygon(0px 0px, 0% 0, 0% 100%, 0px 100%);
  }
  100% {
    -webkit-clip-path: polygon(0px 0px, 100% 0, 100% 100%, 0px 100%);
            clip-path: polygon(0px 0px, 100% 0, 100% 100%, 0px 100%);
  }
}

