.big-circle {
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background: linear-gradient(to bottom, #006699, #617e8d);
  bottom: 50%;
  right: 50%;
  -webkit-transform: translate(-40%, 38%);
          transform: translate(-40%, 38%)
}

.big-circle:after {
  content: "";
  position: absolute;
  width: 360px;
  height: 360px;
  background-color: #fafafa;
  border-radius: 50%;
  top: calc(50% - 180px);
  left: calc(50% - 180px)
}

.square {
  position: absolute;
  height: 400px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(181%, 11%);
