@keyframes pulse-border {
	0% {
		padding: 25px;
		opacity: 0.75;
	}
	75% {
		padding: 30px;
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}

.scrollTop {
	position: fixed; 
	bottom: 20px;
	right: 20px;
	width: 40px;
	height: 40px;
	font-size: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	cursor: pointer;
	opacity: 0.5;
	background-color: white;
	border-radius: 50%;
	animation: fadeIn 0.3s;
	transition: opacity 0.3s;
}
  
.scrollTop:hover{
	opacity: 1;
}
  
@keyframes fadeIn {
	0% {
	  opacity: 0;
	}
	100% {
	  opacity: 0.5;
	}
}